import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { ElModule } from 'element-angular/release/element-angular.module';
import { ProjectListComponent } from './project-list/project-list.component';
import { ProductListComponent } from './product-list/product-list.component';
import { DeviceListComponent } from './device-list/device-list.component';
import { ProjectAddComponent } from './project-add/project-add.component';
import { ProductAddComponent } from './product-add/product-add.component';
import { DeviceAddComponent } from './device-add/device-add.component';
import { ProjectTemplateListComponent } from './project-template-list/project-template-list.component';
import { TemplateAddComponent } from './template-add/template-add.component';
import { AddComponent } from './add/add.component';

export const projectManageRoutes = [
  {
    path: '',
    children: [
      {path: '', redirectTo: 'projectList', pathMatch: 'full'},
      {path: 'add', component: AddComponent},
      {path: 'projectList', component: ProjectListComponent},
      {path: 'projectAdd', component: ProjectAddComponent},
      {path: 'productList', component: ProductListComponent},
      {path: 'deviceList', component: DeviceListComponent},
      {path: 'projectTemplateList', component: ProjectTemplateListComponent},
      {path: 'templateAdd', component: TemplateAddComponent},
    ]
  }
];

@NgModule({
  declarations: [
    ProjectListComponent,
    ProductListComponent,
    DeviceListComponent,
    ProjectAddComponent,
    ProductAddComponent,
    DeviceAddComponent,
    ProjectTemplateListComponent,
    TemplateAddComponent,
    AddComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(projectManageRoutes),
    ElModule
  ],
  exports: [
    AddComponent
  ]
})
export class ProjectManageModule {
}
